import React, { Component } from 'react';
import { Row, Col, Form, Input, DatePicker, Checkbox, Select, message } from 'antd';
import { SelectAjax, TableData } from 'components/common/index';
import { getTrainTypes } from 'services/trainType';
import { getStudyFields } from 'services/StudyField';
import {
  getPaperEnrollments,
} from 'services/PaperEnrollment';
import {
  receiveEnrollNote,
  exportEnrollNote,
} from 'services/Candidate';
import { BtnSave, BtnExport } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import moment from 'moment';

const paperColumns = [
  {
    title: 'Bắt buộc',
    dataIndex: 'isMandantory',
    key: 'isMandantory',
    render: isMandantory => isMandantory ? 'Y' : 'N',
  },
  {
    title: 'Tên giấy tờ',
    dataIndex: 'paperTypeName',
    key: 'paperTypeName',
  },
  {
    title: 'Số lượng',
    dataIndex: 'noOfPieces',
    key: 'noOfPieces',
    // render: (noOfPieces,record) => {
    //   console.debug(noOfPieces);
    //   return (
    //     <Input defaultValue={noOfPieces} onChange={e => {record.noOfPieces = e.target.value; return record;}} />
    //   );
    // },
  },
  {
    title: 'Số lượng thực tế',
    dataIndex: 'noOfPiecesReal',
    key: 'noOfPiecesReal',
    render: (noOfPiecesReal,record) => <Input onChange={e => {record.noOfPiecesReal = e.target.value; return record;}} />,
  },
  {
    title: 'Hoàn thành',
    dataIndex: 'isFinished',
    key: 'isFinished',
    render: (isFinished, record) => {
      console.debug(isFinished);
      return (
        <div style={{display: 'flex',justifyContent: 'center'}} >
          <Checkbox onChange={e => {record.isFinished = e.target.checked; return record;}} defaultChecked={false} />
        </div>
      );
    },
  },
];

@Form.create({ name: 'FormInfoReceive' })
class FormInfoReceive extends Component {
  constructor(props) {
    super(props);
    this.state = {
      reload: 0,
      paramSearch: {},
      refTable: React.createRef(),
    };
  }

  componentWillReceiveProps = nextProps => {
    if (nextProps.selectedStudent && 
      nextProps.selectedStudent !== this.props.selectedStudent) {
      this.setState({
        paramSearch: {
          trainTypeId: nextProps.selectedStudent.trainTypeId,
          candidateId: nextProps.selectedStudent.id,
        },
      });
      this.props.form.setFieldsValue({
        enrolledDate: moment(nextProps.selectedStudent.enrolledDate),
        enrolledById: nextProps.selectedStudent.enrolledById,
        trainTypeId: nextProps.selectedStudent.trainTypeId,
        certTypeId: parseInt(nextProps.selectedStudent.certTypeId),
        studyFieldId: nextProps.selectedStudent.studyFieldId,
      });
    }
  };

  onChangeTrainTypes = async value => {
    const params = Object.assign({} , this.state.paramSearch);
    params.trainTypeId = value;
    this.setState({
      paramSearch: params,
    });
  };

  handleSave = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        console.log('err',err)
      } else {
        if (values.enrolledDate) {
          values.enrolledDate = moment(values.enrolledDate).format("YYYY-MM-DD");
        }
        const dataOfTable = [...this.state.refTable.current.state.data];
        const paperList = [];
        for (let index = 0; index < dataOfTable.length; index += 1) {
          const element = Object.assign({},dataOfTable[index]);
          let {noOfPiecesReal,isFinished} = element;
          const {id} = element;
          noOfPiecesReal = !Number.isNaN(parseInt(noOfPiecesReal)) ? parseInt(noOfPiecesReal) : 0;
          isFinished = isFinished !== null ? isFinished : false;
          paperList.push({id, noOfPiecesReal, isFinished});
        }
        const requestParam = {
          ...values, 
          paperList,
          candidateId: this.props.selectedStudent.id,
          code: this.props.selectedStudent.code,
          enrolledById: this.props.selectedStudent.enrolledById,  
          token:localStorage.getItem('app.token'),
        };
        console.log('requestParam',requestParam);
        receiveEnrollNote(requestParam).then(() => {
          message.success('Nhận hồ sơ thành công');
        })
        .catch(error => {
          console.log(error);
          message.error('Có lỗi xảy ra');
        });;
      }
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSave}>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Ngày nhập học">
              {getFieldDecorator('enrolledDate', {
                initialValue: moment(),
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Ngày nhập học!',
                  },
                ],
                })(<DatePicker style={{width: '100%'}} />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Người tiếp nhận">{getFieldDecorator('enrolledById', {})(<Input disabled />)}</Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Hệ NH">
              {getFieldDecorator('trainTypeId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Hệ NH!',
                  },
                ],
              })(
                <SelectAjax service={getTrainTypes} onChange={this.onChangeTrainTypes} />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={8}>
            <Form.Item label="Trình độ">
              {getFieldDecorator('certTypeId', {
                initialValue: 2,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Trình độ!',
                  },
                ],
              })(
                <Select>
                  <Select.Option value={2}>Trung học cơ sở</Select.Option>
                  <Select.Option value={1}>Trung học phổ thông</Select.Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ngành đào tạo">
              {getFieldDecorator('studyFieldId', {
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng chọn Ngành đào tạo!',
                  },
                ],
              })(
                <SelectAjax
                  service={getStudyFields}
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label=" ">
              {getFieldDecorator('isSplit', {initialValue: false})(
                <Checkbox >Phân lớp khi gọi nhập học</Checkbox>
              )}
            </Form.Item>
          </Col>
          <Col span={4}>
            <Form.Item label=" ">
              {getFieldDecorator('isExport', {initialValue: false})(<Checkbox>In phiếu khi lưu</Checkbox>)}
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={24}>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              allowGetData={!!this.state.paramSearch.candidateId}
              service={getPaperEnrollments}
              columns={paperColumns}
              rowKey="id"
              ref={this.state.refTable}
            />
          </Col>
        </Row>
        <Row gutter={24}>
          <Col offset={8} span={12}>
            <BtnSave htmlType="submit" />
            <BtnExport onClick={() => exportEnrollNote(getQueryParams())} />
          </Col>
        </Row>
      </Form>
      
    );
  }
}

export default FormInfoReceive;



